.loading-screen {
  z-index: 1;
  opacity: 1;
  background-color: #000;
  width: 100%;
  height: 100%;
  transition: opacity 3s;
  position: absolute;
  top: 0;
  left: 0;
}

.loading-screen.loaded {
  opacity: 0;
}

.play-button {
  z-index: 2;
  background-color: #0000;
  border: 50px solid #0000;
  border-left: 100px solid #eceff1;
  border-right: 0 solid #eceff1;
  width: 100px;
  height: 100px;
  margin-top: -50px;
  margin-left: -50px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.play-button.stop, .play-button.to-play {
  transition: transform .3s cubic-bezier(0, 0, .2, 1), border-top-width .3s cubic-bezier(0, 0, .2, 1), border-bottom-width .3s cubic-bezier(0, 0, .2, 1);
}

.play-button.stop {
  border-width: 0 0 0 100px;
  transform: rotate(90deg);
}

.play-button.to-play {
  border-width: 50px 100px 50px 0;
  transform: rotate(180deg);
}

.play-button.loading {
  border-width: 0 0 0 100px;
  animation: .5s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  background-color: #000;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

main {
  display: flex;
}

main audio {
  z-index: 1;
  opacity: .2;
  transition: all .5s linear;
}

main audio:hover {
  opacity: .8;
}

canvas {
  border: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

